er around the card.\n/// @param {List} $radius - border radius of the card.\n/// @param {List} $shadow - box shadow of the card.\n@mixin card-container(\n  $background: $card-background,\n  $color: $card-font-color,\n  $margin: $card-margin-bottom,\n  $border: $card-border,\n  $radius: $card-border-radius,\n  $shadow: $card-shadow\n) {\n  @if $global-flexbox {\n    display: flex;\n    flex-direction: column;\n    flex-grow: 1;\n  }\n\n  margin-bottom: $margin;\n\n  border: $border;\n  border-radius: $radius;\n\n  background: $background;\n  box-shadow: $shadow;\n\n  overflow: hidden;\n  color: $color;\n\n  & > :last-child {\n    margin-bottom: 0;\n  }\n}\n\n/// Adds styles for a card divider.\n@mixin card-divider(\n  $background: $card-divider-background,\n  $padding: $card-padding\n) {\n  @if $global-flexbox {\n    flex: 0 1 auto;\n    display: flex;\n  }\n\n  padding: $padding;\n  background: $background;\n\n  & > :last-child {\n    margin-bottom: 0;\n  }\n}\n\n/// Adds styles for a card section.\n@mixin card-section(\n  $padding: $card-padding\n) {\n  @if $global-flexbox {\n    flex: 1 0 auto;\n  }\n\n  padding: $padding;\n\n  & > :last-child {\n    margin-bottom: 0;\n  }\n}\n\n@mixin foundation-card {\n  .card {\n    @include card-container;\n  }\n\n  .card-divider {\n    @include card-divider;\n  }\n\n  .card-section {\n    @include card-section;\n  }\n\n  // For IE 11 - Flexbug\n  // https://github.com/philipwalton/flexbugs/issues/75\n  .card-image {\n    min-height: 1px;\n  }\n}\n","// Foundation for Sites by ZURB\n// foundation.zurb.com\n// Licensed under MIT Open Source\n\n////\n/// @group close-button\n////\n\n/// Default position of the close button. The first value should be `right` or `left`, and the second value should be `top` or `bottom`.\n/// @type List\n$closebutton-position: right top !default;\n\n/// Right (or left) offset(s) for a close button.\n/// @type Number|Map\n$closebutton-offset-horizontal: (\n  small: 0.66rem,\n  medium: 1rem,\n) !defau